/* src/App.css */

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  h2 {
    color: #2c3e50; /* Dark blue */
    margin-bottom: 2rem;
  }
  
  a {
    text-decoration: none;
    color: #1abc9c; /* Teal */
  }
  
  a:hover {
    text-decoration: underline;
  }
  